import React, { Component } from 'react'
import arrow from '../styles/images/arrow.svg'
import { Token, SelectTag } from './SelectTag'
import { AppState } from './App';
import { ContractType, getContracts } from "@btcswap/abi"; 
import GatewayJS from "@renproject/gateway";
import { Contract } from 'ethers';
import { defaultAbiCoder } from 'ethers/utils';

interface State {
    sendToken: Token,
    receiveToken: Token,
    receiveBtcAddress: string,
    sendAmount: string,
    receiveAmount: string,
    renBTCAdapter: Contract | null,
    multiBTC: Contract | null,
    wBTC: Contract | null,
    renBTC: Contract | null,
    errorMsg: string,
    showBtcAddressEntry: boolean
}

interface Props {
    web3Provider: AppState["web3Provider"]; 
    gsnProvider: AppState["gsnProvider"];
    gateway: AppState["gateway"];
}

class Swap extends Component<Props, State> {
    state: State = {
        sendToken: Token.BTC,
        receiveToken: Token.WBTC,
        receiveBtcAddress: "",
        renBTCAdapter: null,
        multiBTC: null,
        renBTC: null,
        wBTC: null,
        sendAmount: "0.0",
        receiveAmount: "0.0",
        errorMsg: "",
        showBtcAddressEntry: false,
    }

    async componentDidMount() {
        const { web3Provider, gsnProvider } = this.props;
        if (!web3Provider || !gsnProvider) {
            this.setState({ errorMsg: "web3 providers not initialized" });
            return;
        }

        const web3ContractMap = await getContracts(web3Provider);
        const gsnContractMap = await getContracts(gsnProvider);
        const renBTC = web3ContractMap.get(ContractType.RenBTC);
        const wBTC = web3ContractMap.get(ContractType.WBTC);
        const multiBTC = gsnContractMap.get(ContractType.MultiCollateralBitcoin);
        const renBTCAdapter = gsnContractMap.get(ContractType.RenBTCAdapter);
        if (!renBTC || !wBTC || !multiBTC || !renBTCAdapter) {
            this.setState({ errorMsg: "failed to connect to contracts" });
            return;
        }
        this.setState({ renBTC, wBTC, multiBTC, renBTCAdapter });
    }

    changeSendToken = (sendToken: Token) => {
        const { receiveToken, sendAmount } = this.state;
        this.setState({ sendToken, receiveAmount: this.calcReceiveAmount(sendToken, receiveToken, sendAmount)});
    }

    changeReceiveToken = (receiveToken: Token) => {
        const { sendToken, sendAmount } = this.state;
        this.setState({ receiveToken, showBtcAddressEntry: receiveToken === Token.BTC, receiveAmount: this.calcReceiveAmount(sendToken, receiveToken, sendAmount)});
    }

    swapTokens = () => {
        const { sendToken, receiveToken } = this.state;
        this.setState({ sendToken: receiveToken, receiveToken: sendToken, showBtcAddressEntry: sendToken === Token.BTC });
    }

    handleSendAmount = (event: React.ChangeEvent<HTMLInputElement>) => {
        const { sendToken, receiveToken } = this.state;
        const sendAmount = event.target.value;
        const receiveAmount = this.calcReceiveAmount(sendToken, receiveToken, sendAmount)
        this.setState({ sendAmount, receiveAmount });
    }

    removeOrAddFees = (sendToken: Token, receiveToken: Token, amount: number, addFee: boolean): string => {
        let feeFixed: number;
        let feeVar: number;

        if (sendToken == Token.BTC && receiveToken == Token.MultiBTC) {
            feeFixed = 3500;
            feeVar = 1; 
        } else if (sendToken == Token.BTC && receiveToken == Token.WBTC) {
            feeFixed = 3500;
            feeVar = 2; 
        }  else if (sendToken == Token.WBTC && receiveToken == Token.BTC) {
            feeFixed = 3500;
            feeVar = 2;
        } else if (sendToken == Token.MultiBTC && receiveToken == Token.BTC) {
            feeFixed = 3500;
            feeVar = 2;
        } else if (sendToken == Token.MultiBTC && receiveToken == Token.WBTC) {
            feeVar = 1; 
            feeFixed = 0;
        } else {
            feeVar = 0;
            feeFixed = 0;
        }

        let amtAfterFee;
        if (addFee) {
            amtAfterFee = Math.floor(amount * (10 ** 8) * (1000 - feeVar)/1000) - feeFixed;
        } else {
            amtAfterFee = Math.floor(amount * (10 ** 8) * 1000/(1000-feeVar)) + feeFixed; 
        }

        if ( !amtAfterFee || amtAfterFee < 0) {
            return "0.0"
        } else {
            return (amtAfterFee/(10 ** 8)).toString();
        }
    }

    calcReceiveAmount = (sendToken: Token, receiveToken: Token, sendAmount: string): string => {
        return this.removeOrAddFees(sendToken, receiveToken, parseFloat(sendAmount), true)
    }

    handleRecieveAmount = (event: React.ChangeEvent<HTMLInputElement>) => {
        const receiveAmount = event.target.value;
        const { sendToken, receiveToken } = this.state;
        const sendAmount = this.calcSendAmount(sendToken, receiveToken, receiveAmount);
        this.setState({ sendAmount, receiveAmount });
    }

    calcSendAmount = (sendToken: Token, receiveToken: Token, receiveAmount: string): string => {
        return this.removeOrAddFees(sendToken, receiveToken, parseFloat(receiveAmount), false)
    }

    handleBtcAddress = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({ receiveBtcAddress: event.target.value });
    }

    render() {
        const { showBtcAddressEntry, sendToken, receiveToken, sendAmount, receiveAmount, errorMsg } = this.state;
        return (
            <div className="swap">
                <div className="bg">
                    <p className="text">From</p>
                    <input className="input" type="text" placeholder={sendAmount} value={sendAmount} onChange={this.handleSendAmount}/>
                    <div className="select">
                        <SelectTag changeValue={this.changeSendToken} defaultOption={sendToken} removeToken={receiveToken}/>
                    </div>
                </div>
                <div className="arrow">
                    <img src={arrow} alt="arrow" onClick={this.swapTokens}/>
                </div>
                <div className="bg">
                    <p className="text">To</p>
                    <input className="input" type="text" placeholder={receiveAmount} value={receiveAmount} onChange={this.handleRecieveAmount}/>
                    <div className="select">
                        <SelectTag changeValue={this.changeReceiveToken} defaultOption={receiveToken} removeToken={sendToken}/>
                    </div>
                </div>
                <p className="feetag">Inclusive of all fees</p>
                {
                    showBtcAddressEntry && <div> <input className="btcaddress" type="text" placeholder="Bitcoin Address" onChange={this.handleBtcAddress} /> </div>                 
                }
                <button className="swapbutton" onClick={this.swap}>Swap</button>
                { errorMsg !== "" && <p className="errmsg"> {errorMsg} </p>}
            </div>
        )
    }

    swap = async() => {
        const {sendAmount, sendToken, receiveToken, receiveBtcAddress} = this.state;
        const value = Math.floor(parseFloat(sendAmount) * (10 ** 8));
        console.log(value, sendToken, receiveToken, receiveBtcAddress);

        if (receiveToken === Token.BTC && receiveBtcAddress === "") {
            this.setState({ errorMsg: "please enter a valid receiveBtcAddress"})
            return;
        }

        if (sendToken === Token.MultiBTC) {
            await this.withdraw(receiveBtcAddress, value, receiveToken);
            return;
        }
    
        if (receiveToken === Token.MultiBTC) {
            await this.deposit(value, sendToken);
            return;
        }
    
        if (receiveToken === Token.BTC && sendToken === Token.WBTC) {
            await this.swapWBTCToBTC(receiveBtcAddress, value);
        } else if (receiveToken === Token.WBTC && sendToken === Token.BTC) {
            await this.swapBTCToWBTC(value);
        } else {
            this.setState({ errorMsg: `unsupported coin pair ${receiveToken}->${sendToken}` });
        }
    }

    swapWBTCToBTC = async(toAddress: string, amount: number) => {
        const { web3Provider, gateway } = this.props;
        if (!web3Provider || !gateway) {
            this.setState({ errorMsg: "failed to setup Web3 provider" });
            return;
        }
        const address = await web3Provider.getSigner().getAddress();
        const { renBTC, wBTC, multiBTC, renBTCAdapter } = this.state;

        if (!renBTC || !wBTC || !multiBTC || !renBTCAdapter) {
            this.setState({ errorMsg: "failed to initialize contracts"});
            return;
        }

        const balance = await renBTC.balanceOf(multiBTC.address);
        if (balance < amount) {
            this.setState({ errorMsg: `insufficent BTC in the wrapper contract (${balance})` });
            return;
        }

        const allowance = await wBTC.allowance(address, renBTCAdapter.address);
        if (allowance < amount) {
            await wBTC.approve(renBTCAdapter.address, amount.toString());
        }
    
        await gateway.open({
            // Send BTC from the Ethereum blockchain to the Bitcoin blockchain.
            // This is the reverse of shitIn.
            sendToken: GatewayJS.Tokens.BTC.Eth2Btc,
    
            // The contract we want to interact with
            sendTo: renBTCAdapter.address,
    
            // The name of the function we want to call
            contractFn: "swapOut",
    
            // Arguments expected for calling `deposit`
            contractParams: [
            { name: "_fromBtcVariant", type: "address", value: wBTC.address },
            { name: "_to", type: "bytes", value: "0x" + Buffer.from(toAddress).toString("hex") },
            { name: "_amount", type: "uint256", value: Math.floor(amount) },
            ],
    
            // Web3 provider for submitting burn to Ethereum
            web3Provider: web3Provider,
        }).result();
    
        this.log(`Transferred ${amount} BTC to ${toAddress}.`);
        }
    
    swapBTCToWBTC = async(amount: number) => {
        const { gsnProvider, gateway } = this.props;
        if (!gsnProvider || !gateway) {
            this.setState({ errorMsg: "failed to setup Web3 provider" });
            return;
        }
        const { wBTC, multiBTC, renBTC } = this.state;

        if (!renBTC || !wBTC || !multiBTC) {
            this.setState({ errorMsg: "failed to initialize contracts"});
            return;
        }

        const balance = await wBTC.balanceOf(multiBTC.address);
        if (balance < amount) {
            this.setState({ errorMsg: `insufficent wBTC in the wrapper contract (${balance})` });
            return;
        }

        try {
            await gateway.open({
            // Send BTC from the Bitcoin blockchain to the Ethereum blockchain.
            sendToken: GatewayJS.Tokens.BTC.Btc2Eth,
    
            // Amount of BTC we are sending (in Satoshis)
            suggestedAmount: Math.floor(amount), // Convert to Satoshis
    
            // The contract we want to interact with
            sendTo: renBTC.address,
    
            // The name of the function we want to call
            contractFn: "swapIn",
    
            // The nonce is used to guarantee a unique deposit address
            nonce: GatewayJS.utils.randomNonce(),
    
            // Arguments expected for calling `deposit`
            contractParams: [
                {
                name: "_msg",
                type: "bytes",
                value: defaultAbiCoder.encode(["address"], [wBTC.address]),
                }
            ],
    
            // Web3 provider for submitting mint to Ethereum
            web3Provider: gsnProvider,
            }).result();
            this.log(`Deposited ${amount} BTC.`);
        } catch (error) {
            // Handle error
            this.logError(error);
        }
    }   
    
    // recoverTransfers = async() => {
    //     const { web3Provider, gateway } = this.props;
    //     if (!gateway || !web3Provider) {
    //         this.setState({ errorMsg: "failed to initialize web3 provider" });
    //         return;
    //     }

    //     // Load previous transfers from local storage
    //     const previousGateways = await gateway.getGateways();
    //     // Resume each transfer
    //     for (const transfer of Array.from(previousGateways.values())) {
    //         gateway
    //         .recoverTransfer(httpProvider, transfer)
    //         .pause()
    //         .result()
    //         .catch(this.logError);
    //     }
    // }
    
    deposit = async(value: number, coinType: string) => {
        switch (coinType) {
          case "btc":
              await this.depositBtc(value);
            break;
          case "wbtc":
              await this.depositWBTC(value);
            break;
          default:
              this.setState({ errorMsg: `unsupported coin type ${coinType}` });
            break;
        }
    }
    
      depositWBTC = async(amount: number) => {
        const { web3Provider } = this.props;
        if (!web3Provider) {
            this.setState({ errorMsg: "failed to setup Web3 provider" });
            return;
        }
        const address = await web3Provider.getSigner().getAddress();
        const { wBTC, multiBTC } = this.state;
        if (!wBTC || !multiBTC) {
            this.setState({ errorMsg: "failed to initialize contracts"});
            return;
        }
    
        const allowance = await wBTC.allowance(address, multiBTC.address);
    
        if (allowance < amount) {
          await wBTC.approve(multiBTC.address, amount.toString());
        }
        await multiBTC.deposit(wBTC.address, amount.toString());
    }

    depositBtc = async(amount: number) => {
        const { gsnProvider, gateway } = this.props;
        if (!gsnProvider || !gateway) {
            this.setState({ errorMsg: "failed to setup Web3 provider" });
            return;
        }
        
        const { renBTC } = this.state;

        if (!renBTC) {
            this.setState({ errorMsg: "failed to initialize contracts"});
            return;
        }

        try {
          await gateway.open({
            // Send BTC from the Bitcoin blockchain to the Ethereum blockchain.
            sendToken: GatewayJS.Tokens.BTC.Btc2Eth,
    
            // Amount of BTC we are sending (in Satoshis)
            suggestedAmount: amount, // Convert to Satoshis
    
            // The contract we want to interact with
            sendTo: renBTC.address,
    
            // The name of the function we want to call
            contractFn: "deposit",
    
            // The nonce is used to guarantee a unique deposit address
            nonce: GatewayJS.utils.randomNonce(),
    
            // Arguments expected for calling `deposit`
            contractParams: [
              {
                name: "_msg",
                type: "bytes",
                value: "0x" + new Buffer(`Convert ${amount} BTC to MultiBTC`).toString("hex"),
              }
            ],

            // Web3 provider for submitting mint to Ethereum
            web3Provider: gsnProvider,
          }).result();
          this.log(`Deposited ${amount} BTC.`);
        } catch (error) {
          // Handle error
          this.logError(error);
        }
      }
    
    withdraw = async(toBtcAddress: string, value: number, coinType: string) => {
        switch (coinType) {
          case "btc":
              await this.withdrawBTC(toBtcAddress, value);
            break;
          case "wbtc":
              await this.withdrawWBTC(value);
            break;
          default:
              this.setState({ errorMsg: `unsupported coin type ${coinType}` });
            break;
        }
      }
    
      withdrawBTC = async(toBtcAddress: string, amount: number) => {
        const { web3Provider, gateway } = this.props;
        if (!web3Provider || !gateway) {
            this.setState({ errorMsg: "failed to setup Web3 provider" });
            return;
        }
        const address = await web3Provider.getSigner().getAddress(); 
        const { wBTC, multiBTC, renBTC } = this.state;

        if (!renBTC || !wBTC || !multiBTC) {
            this.setState({ errorMsg: "failed to initialize contracts"});
            return;
        }

        const allowance = await multiBTC.allowance(address, renBTC.address);
        if (allowance < amount) {
            await multiBTC.approve(renBTC.address, amount.toString());
        }

        // You can surround shiftOut with a try/catch to handle errors.
        await gateway.open({
            // Send BTC from the Ethereum blockchain to the Bitcoin blockchain.
            // This is the reverse of shitIn.
            sendToken: GatewayJS.Tokens.BTC.Eth2Btc,
    
            // The contract we want to interact with
            sendTo: renBTC.address,
    
            // The name of the function we want to call
            contractFn: "withdraw",
    
            // Arguments expected for calling `deposit`
            contractParams: [
            { name: "_to", type: "bytes", value: "0x" + Buffer.from(toBtcAddress).toString("hex") },
            { name: "_amount", type: "uint256", value: Math.floor(amount) },
            ],
    
            // Web3 provider for submitting burn to Ethereum
            web3Provider: web3Provider,
        }).result();
        
        this.log(`Withdrew ${amount} BTC to ${toBtcAddress}.`);    
      } 
    
      withdrawWBTC = async(amount: number) => {
        const { multiBTC, wBTC } = this.state;
        if (!wBTC || !multiBTC) {
            this.setState({ errorMsg: "failed to initialize contracts"});
            return;
        }
        await multiBTC.withdraw(wBTC.address, amount.toString());
      }

      log = async(message: string) => {
        console.log(message);
      }

      logError = async(message: string) => {
          console.error(message);
      }
}

export default Swap