import { Web3Provider } from "ethers/providers";
import {  Contract } from "ethers";
import Config from "./config.json";

export interface GSNConfig {
    RelatHub: string,
    Paymaster: string,
    StakeManager: string,
}

export enum ContractType {
  RenBTCAdapter = "RenBTCAdapter",
  MultiCollateralBitcoin = "MultiCollateralBitcoin",
  RenBTC = "RenBTC",
  WBTC = "WBTC",
}

const SupportedContracts = [ContractType.RenBTCAdapter, ContractType.MultiCollateralBitcoin, ContractType.RenBTC, ContractType.WBTC ]

export const GSNConfigs = new Map<string, GSNConfig>();

GSNConfigs.set("kovan", {
    RelatHub: "0x2E0d94754b348D208D64d52d78BcD443aFA9fa52",
    Paymaster: "0x957F270d45e9Ceca5c5af2b49f1b5dC1Abb0421c",
    StakeManager: "0x0ecf783407C5C80D71CFEa37938C0b60BD255FF8",
});

export async function getContracts(provider: Web3Provider): Promise<Map<ContractType, Contract | null >> {
  const net = await provider.getNetwork();
  const contracts = new Map<ContractType, Contract | null >();
  const network = net.name === "unknown" ? "development" : net.name;
  SupportedContracts.forEach(ctype => {
      const artifact = (Config as any)[network][ctype];
      console.log(ctype, artifact.Address);
      return contracts.set(ctype, new Contract(artifact.Address, artifact.ABI, provider.getSigner()));
  });
  return contracts;
}