import * as React from "react";

import Select, { components, OptionProps, SingleValueProps, ValueType } from "react-select";

import { ReactComponent as IconBTC } from "../styles/images/btclogo.svg"; 
import { ReactComponent as IconWBTC } from "../styles/images/wbtclogo.svg";
import { ReactComponent as IconMultiBTC } from "../styles/images/multibtclogo.svg";

interface Props {
    changeValue: (token: Token) => void;
    removeToken?: Token;
    defaultOption: Token;
    disabled?: boolean;
}

interface State {
}

export enum Token {
    BTC = "btc",
    WBTC = "wbtc",
    MultiBTC = "multibtc",
}

const options = [
    { value: "btc", label: "BTC", icon: <IconBTC /> },
    { value: "wbtc", label: "WBTC", icon: <IconWBTC /> },
    { value: "multibtc", label: "multiBTC", icon: <IconMultiBTC /> },
];

export class SelectTag extends React.Component<Props, State> {
    constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
        };
        this.handleChange = this.handleChange.bind(this);
    }

    public render(): JSX.Element {
        const { defaultOption, removeToken, disabled } = this.props;

        let selectedOption: ValueType<any>;
        switch (defaultOption) {
            case Token.BTC:
                selectedOption = options[0];
                break;
            case Token.WBTC:
                selectedOption = options[1];
                break;
            case Token.MultiBTC:
                selectedOption = options[2];
                break;
        }

        const selectOptions: ValueType<any>[] = [];
        options.forEach((option) => {
            if (removeToken && option.value !== removeToken) {
                selectOptions.push(option);
            }
        })

        return (
            <div className="select">
                <Select
                    isDisabled={disabled}
                    components={{
                        SingleValue: customSingleValue,
                        Option: customOption,
                    }}
                    value={selectedOption}
                    options={selectOptions}
                    clearIndicator={true}
                    isSearchable={false}
                    styles={customStyles}
                    onChange={this.handleChange}
                />
            </div>
        );
    }

    private handleChange (selected: ValueType<any>): void {
        this.setState({ selected });
        const {changeValue} = this.props;
        changeValue(selected.value);
    }
}

const customSingleValue = (props: SingleValueProps<any>) => {
    const { SingleValue } = components;
    return (
        <SingleValue {...props}>
            {props.data.icon &&
                <span className="icon">{props.data.icon}</span>
            }
            <span className="label">{props.data.label}</span>
        </SingleValue>
    );
};

const customOption = (props: OptionProps<any>) => {
    const { Option } = components;
    return (
        <Option {...props}>
            {props.data.icon &&
                <span className="icon">{props.data.icon}</span>
            }
            {props.data.label}
        </Option>
    );
};

const width = "170px";
const customStyles = {
    control: () => ({
        display: "flex",
        border: "none",
        width,
        cursor: "default",
    }),
    singleValue: (provided: React.CSSProperties) => ({
        ...provided,
        fontSize: "20px",
        display: "flex",
        alignItems: "center",
    }),
    menu: (provided: React.CSSProperties) => ({
        ...provided,
        fontSize: "18px",
        display: "flex",
        width,
        marginTop: "20px",
        boxShadow: "0 3px 6px 0 rgba(0, 0, 0, 0.05)",
        borderRadius: "30px",
        cursor: "default",
    }),
    option: (provided: React.CSSProperties, state: OptionProps<any>) => ({
        display: "flex",
        alignItems: "center",
        height: "60px",
        width,
        padding: "0 15px",
    }),
    indicatorSeparator: () => ({
        display: "none",
    }),
};