import { HttpProvider } from "web3-core";

interface InjectedEthereum extends HttpProvider {
    enable: () => Promise<void>;
}

declare global {
    interface Window {
        ethereum?: InjectedEthereum;
    }
}

export const getHttpProvider = async () => new Promise<HttpProvider>(async (resolve, reject) => {
    // Modern dApp browsers...
    if (window.ethereum) {
        try {
            // Request account access if needed
            await window.ethereum.enable();
            resolve(window.ethereum);
        } catch (error) {
            reject(error);
        }
    } else {
        reject("cannot connect to ethereum");
    }
});
