import React from 'react'
import btcswaplogo from '../styles/images/btcswaplogo.png'
import { AppActions, AppState } from "./App";
import { Web3Provider } from 'ethers/providers';
import CopyToClipboard  from 'react-copy-to-clipboard';

interface Props {
    initGSN: AppActions["initGSN"];
    address: AppState["address"];
    gsnProvider: AppState["gsnProvider"];
    web3Provider: AppState["web3Provider"];
}

interface State {
    copied: boolean
    buttonMsg: string
}

class Header extends React.Component<Props, State> {
    constructor(props: Props, context: object) {
        super(props, context);
        this.state = {
            copied: false,
            buttonMsg: ""
        };
    }

    onCopy = () => {
        this.setState({ buttonMsg: "Copied!" });
        setTimeout(() => {
            this.setState({buttonMsg:""});
        }, 1000);    };

    componentDidMount() {
        const { initGSN } = this.props;
        initGSN();
    }

    async getAddressTag(web3Provider: Web3Provider): Promise<string> {
        return web3Provider.getSigner().getAddress();
    }

    render(): JSX.Element {
        const { address, gsnProvider, web3Provider } = this.props;
        const { buttonMsg } = this.state;

        return (
            <div className="header">
                { !gsnProvider && !web3Provider && <div className="errMsg">
                    <p>Please connect to mainnet or kovan on your dapp browser</p>
                </div>}
                <div className="dappHeader">
                <img src={btcswaplogo} alt="btcswaplogo" />
                { web3Provider ?
                    <CopyToClipboard
                        onCopy={this.onCopy}
                        text={address}>
                    <text>{buttonMsg == "" ? `${address.substr(0, 6)}...${address.slice(-4)}`: buttonMsg}</text>
                    </CopyToClipboard>: <button>Connect</button>}
                </div>
            </div>
        );
    }
}

export default Header;
